import {
  Modal,
  StyleSheet,
  ScrollView,
  Text,
  TextInput,
  TouchableOpacity,
  TouchableWithoutFeedback,
  View,
  Image,
  Platform,
  FlatList,
  Dimensions,
} from "react-native";
import Octicons from "react-native-vector-icons/Octicons";
