import ChatController, { Props, configJSON, IChat, IChatResponse } from "./ChatController";
import globalStyles, { dimension } from "../../utilities/src/GlobalStyles";
import { imgArrowBack, imgDelete, imgMessage, imgNewChat, imgSearch } from "./assets";
import { COLORS, FONTS } from "../../../components/src/AppGlobals";
import { normalize } from "react-native-elements";
import FastImage from "react-native-fast-image";
import { Constant, defaultProfileImage } from "../../../framework/src/AppConstant";
import moment from "moment";
import { getTimeFromDateString } from "../../../framework/src/BlockHelper";
import { SwipeListView } from 'react-native-swipe-list-view';

export default class Chat extends ChatController {
  constructor(props: Props) {
    super(props);
    // Customizable Area Start
    // Customizable Area End
  }

  // Customizable Area Start
  renderItem = (item: IChatResponse) => {
    let userPic = item.attributes.profile_info.profile_pic ?? ''
    let userName = item.attributes.profile_info.user_name ?? ''
    let message = item.attributes.message?.message ?? ''
    let isRead = item.attributes.message?.is_mark_read ?? true
    let createdAt = item.attributes.message?.created_at ?? ''
    let time = getTimeFromDateString(createdAt)//createdAt ? moment.utc(createdAt).local().startOf('seconds').fromNow() : ""
    return (
      <TouchableOpacity
        testID="rowView"
        style={{
          paddingLeft: normalize(1),
          paddingRight: normalize(1),
          paddingTop: normalize(5),
          paddingBottom: normalize(5),
          marginTop: normalize(2),
          marginBottom: normalize(5)
        }}
        onPress={() => {
          // this.handleRowItemOnPress(item);
        }}
      >
        <View style={{ flexDirection: "row" }}>
          <View style={styles.profilePhotoView}>
            <View style={styles.profileCameraView}>
              <FastImage
                source={{
                  uri: userPic
                    ? userPic : defaultProfileImage(userName),
                }}
                style={styles.profileImageView}
                resizeMode={FastImage.resizeMode.stretch}
              />
            </View>
          </View>

          <View
            style={{
              flex: 1,
              flexDirection: "column",
              justifyContent: "center",
              marginLeft: normalize(5),
            }}
          >
            <Text style={[styles.itemName, { alignSelf: "flex-start" }]} numberOfLines={1}>{userName}</Text>
            <Text
              style={[styles.itemName, { color: COLORS.textColorOffWhite, alignSelf: 'flex-start' }]}
              numberOfLines={1}
            >
              {message} <Text style={{ color: COLORS.textColorBlue }}>- {time}</Text>
            </Text>
          </View>
          {!isRead && <Text style={{ color: COLORS.textColorBlue, fontSize: normalize(dimension.bulletText), alignSelf: 'center' }}>{Constant.markBullet} </Text>}

        </View>
      </TouchableOpacity>
    );
  }

  renderHiddenItem = (item: IChatResponse) => {
    return (
      <View style={styles.rowBack}>
        <View style={[styles.backRightBtn, styles.backRightBtnRight]}
        >
          <TouchableOpacity onPress={() => {
            this.handleRowItemOnMessagePress(item);
          }}>
            <View style={{ backgroundColor: COLORS.grey, borderRadius: normalize(30), padding: normalize(10), marginBottom: normalize(8), marginLeft: normalize(10), marginRight: normalize(10) }}>
              <Image
                source={imgMessage}
                style={styles.rowSwipeImage}
                resizeMode="contain"
              />
            </View>
          </TouchableOpacity>
          <TouchableOpacity onPress={() => {
            this.handleRowItemOnDeletePress(item);
          }}>
            <View style={{ backgroundColor: 'red', borderRadius: normalize(30), padding: normalize(10), marginBottom: normalize(8) }}>
              <Image
                source={imgDelete}
                style={styles.rowSwipeImage}
                resizeMode="contain"
              />
            </View>
          </TouchableOpacity>
        </View>
      </View>
    )
  };
  // Customizable Area End

  render() {
    // Customizable Area Start
    // Merge Engine - render - Start

    return (
      <ScrollView keyboardShouldPersistTaps="always" style={styles.container}>
        <TouchableWithoutFeedback
          testID={"hideKeyboard"}
          onPress={() => {
            this.hideKeyboard();
          }}
        >
          <View style={globalStyles.viewWindow}>
            {/* header */}
            <View
              style={[
                globalStyles.flexDirectionRow,
                globalStyles.headerRow,
              ]}
            >
              <TouchableOpacity onPress={this.doButtonBackPressed}>
                <Image
                  source={imgArrowBack}
                  style={globalStyles.headerImage}
                  resizeMode="contain"
                />
              </TouchableOpacity>

              {/* view selection */}
              <View style={styles.selectionView}>
                <TouchableOpacity
                  testID="btnWatcher"
                  style={[
                    styles.buttonView,
                    this.state.dmSelected
                      ? styles.buttonActive
                      : styles.buttonDefault,
                  ]}
                  onPress={this.onPressDm}
                >
                  <Text style={styles.buttonText}>{configJSON.DM}</Text>
                </TouchableOpacity>
                <TouchableOpacity
                  testID="btnWatching"
                  style={[
                    styles.buttonView,
                    this.state.pmSelected
                      ? styles.buttonActive
                      : styles.buttonDefault,
                  ]}
                  onPress={this.onPressPm}
                >
                  <Text style={styles.buttonText}>{configJSON.PM}</Text>
                </TouchableOpacity>
              </View>

              <TouchableOpacity onPress={this.doButtonPressNewMessage}>
                <Image
                  source={imgNewChat}
                  style={globalStyles.headerImage}
                  resizeMode="contain"
                />
              </TouchableOpacity>
            </View>

            <View style={styles.searchImageView}>
              <Image
                source={imgSearch}
                style={styles.imageSearch}
                resizeMode="contain"
              />
              <TextInput
                testID="txtInput"
                style={styles.bgMobileInput}
                placeholder={configJSON.search}
                {...this.txtInputProps}
                onSubmitEditing={this.onPressSubmitting}
              />
            </View>

            {this.state.message.length > 0 && (
              <Text style={[globalStyles.apiMessage]}>{this.state.message}</Text>
            )}
            <SwipeListView
              style={styles.flatList}
              disableRightSwipe
              data={this.state.chatList}
              keyExtractor={item => item.attributes.id + ""}
              renderHiddenItem={(data, rowMap) => (
                <View style={styles.rowBack}>
                  {this.renderHiddenItem(data.item)}
                </View>
              )}
              renderItem={(data, rowMap) => (
                <View style={styles.rowFront}>
                  {this.renderItem(data.item)}
                </View>
              )}
              rightOpenValue={-125}
              closeOnRowOpen
              onRowOpen={(rowKey, rowMap) => {
                setTimeout(() => {
                  rowMap[rowKey].closeRow()
                }, 2000)
              }}
            />

          </View>
        </TouchableWithoutFeedback>
      </ScrollView>
    );
    // Merge Engine - render - End
    // Customizable Area End
  }
}

